import React, { useState, useEffect } from "react";
import axios from "axios";

import Song from "../../components/Main/Song/index";
import "./style.scss";
import { URL } from "../../constants/url.constant";

const NewRelease = (props) => {
  const { onPlayList } = props;
  const [list, setList] = useState([]);

  const date = new Date();
  const dd =
    date.getDate().toString().length >= 2
      ? date.getDate()
      : "0" + date.getDate();
  const mm =
    (date.getMonth() + 1).toString().length >= 2
      ? date.getMonth() + 1
      : "0" + (date.getMonth() + 1);
  const yyyy = date.getFullYear();
  const hours =
    date.getHours().toString().length >= 2
      ? date.getHours()
      : "0" + date.getHours();
  const minutes =
    date.getMinutes().toString().length >= 2
      ? date.getMinutes()
      : "0" + date.getMinutes();

  useEffect(() => {
    const getNewReleaseAPI = async () => {
      const data = await axios
        .get(URL + "/new-release")
        .then((res) => res.data);
      if (data.status === "success") {
        setList(data.doc);
      }
    };

    getNewReleaseAPI();
  }, []);

  return (
    <div className="w-full h-96 relative new-release">
      <div className="w-full h-full absolute top-0 new-release__bg"></div>
      <div className="w-full h-full absolute top-0 bg-gradient-to-br from-sweet-pink via-sweet-pink to-search opacity-90 new-release__screen"></div>

      <div className="w-full mt-20 absolute top-0 sm:pb-24 pb-14">
        <div className="mb-4 sm:mx-10 mx-4">
          <h1 className="text-4xl font-bold text-pink-400">Mới Phát Hành</h1>
          <p className="text-xs font-bold text-gray-500">
            Cập nhật lần cuối lúc {dd + "/" + mm + "/" + yyyy}{" "}
            {hours + ":" + minutes}
          </p>
        </div>

        <div className="sm:mx-12 mt-4 overflow-hidden duration-300 relative z-10">
          {list.length > 0
            ? list.map((item, index) => (
                <Song
                  key={item.encodeId}
                  position={index + 1}
                  item={item}
                  onPlayList={onPlayList}
                />
              ))
            : ""}
        </div>
      </div>
    </div>
  );
};

export default NewRelease;
