import React, { useState, useEffect, useRef } from "react";
import axios from "axios";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faPlus,
  faChevronRight,
  faPlay,
} from "@fortawesome/free-solid-svg-icons";

import Song from "../../components/Main/Song/index";

import avatar from "../../assets/images/default-avatar.jpg";
import "./style.scss";
import { URL } from "../../constants/url.constant";

const User = (props) => {
  const {
    onPlayList,
    userInfo,
    onUserForm__visible,
    onUserPlaylist__play,
  } = props;
  const [playlist, setPlaylist] = useState([]);
  const getIntervalTime = useRef(null);

  //HANDLE SLIDE CHANGE
  useEffect(() => {
    const handleSlider = () => {
      const slideItems = document.getElementsByClassName("slide-item");

      let i = 1;
      getIntervalTime.current = setInterval(() => {
        const prev = i - 1 < 0 ? slideItems.length - 1 : i - 1;
        const next = i + 1 >= slideItems.length ? 0 : i + 1;

        slideItems[prev].classList.remove("first");
        slideItems[prev].classList.remove("third");
        slideItems[prev].classList.add("third");

        slideItems[i].classList.remove("second");
        slideItems[i].classList.remove("first");
        slideItems[i].classList.add("first");

        slideItems[next].classList.remove("third");
        slideItems[next].classList.remove("second");
        slideItems[next].classList.add("second");

        i++;

        if (i >= slideItems.length) {
          i = 0;
        }
      }, 2000);
    };

    if (playlist.length >= 3) {
      handleSlider();
    }

    return () => {
      if (getIntervalTime.current) {
        clearInterval(getIntervalTime.current);
      }
    };
  }, [playlist]);

  useEffect(() => {
    const getSongApi = async () => {
      if (userInfo.playlist.length === 0) {
        return;
      }

      let playList = [];

      await Promise.all(
        userInfo.playlist.map(async (id) => {
          const song = await axios
            .get(URL + `/mp3/${id}`)
            .then((res) => res.data);

          if (song.status === "success") {
            playList.push(song.doc);
          }
        })
      );

      setPlaylist(playList);
    };

    if (userInfo) {
      getSongApi();
    }
  }, [userInfo]);

  if (!userInfo) {
    onUserForm__visible();
    return <div></div>;
  }

  const handlePlaylist = () => {
    onUserPlaylist__play(userInfo.playlist);
  };

  return (
    <div className="pt-20 sm:pb-24 pb-16 sm:mx-6 mx-0">
      <div className="flex flex-col items-center">
        <img className="mb-2 flex-0 rounded-full" src={avatar} />
        <h1 className="text-center text-gray-700 font-bold text-3xl">
          {userInfo.firstName} {userInfo.lastName}
        </h1>
      </div>

      <div className="mt-8 sm:px-4 px-2">
        <div className="mb-4 flex">
          <h1 className="font-bold text-lg text-gray-700 hover:text-pink-400 cursor-pointer">
            Bài Hát
            <FontAwesomeIcon className="ml-2" icon={faChevronRight} />
          </h1>
          <button onClick={handlePlaylist} className="playlist--play">
            <FontAwesomeIcon className="mr-2" icon={faPlay} />
            PHÁT TẤT CẢ
          </button>
        </div>

        <div className="lg:h-60 flex lg:flex-row flex-col lg:items-start items-center">
          <div className="w-72 h-60 mb-8 mr-4 flex-none relative">
            {playlist.length > 0
              ? playlist.map((item, index) => {
                  if (index === 0) {
                    return (
                      <div
                        onClick={() => {
                          onPlayList([item.album.encodeId]);
                        }}
                        className="slide-item first"
                      >
                        <img className="w-full" src={item.thumbnailM} />
                      </div>
                    );
                  }

                  if (index === 1) {
                    return (
                      <div
                        onClick={() => {
                          onPlayList([item.album.encodeId]);
                        }}
                        className="slide-item second"
                      >
                        <img className="w-full" src={item.thumbnailM} />
                      </div>
                    );
                  }

                  return (
                    <div
                      onClick={() => {
                        onPlayList([item.album.encodeId]);
                      }}
                      className="slide-item third"
                    >
                      <img className="w-full" src={item.thumbnailM} />
                    </div>
                  );
                })
              : ""}
          </div>
          <div className="lg:w-auto w-full h-60 px-2 lg:flex-1 overflow-auto">
            {playlist.length > 0
              ? playlist.map((item) => (
                  <Song item={item} onPlayList={onPlayList} />
                ))
              : ""}
          </div>
        </div>
      </div>

      <div className="mt-10 sm:px-4 px-2">
        <div className="sm:mb-4 mb-2 flex">
          <h1 className="font-bold text-lg text-gray-700 hover:text-pink-400 cursor-pointer">
            Playlist
            <FontAwesomeIcon className="ml-2" icon={faChevronRight} />
          </h1>
        </div>

        <div className="flex">
          <div className="playlist__new">
            <FontAwesomeIcon className="mb-4 text-3xl" icon={faPlus} />
            <h1>Tạo playlist mới</h1>
          </div>
        </div>
      </div>
    </div>
  );
};

export default User;
