import React, { useEffect, useState } from "react";
import io from "socket.io-client";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTimes } from "@fortawesome/free-solid-svg-icons";
import { faGooglePlay } from "@fortawesome/free-brands-svg-icons";
import { faCommentAlt } from "@fortawesome/free-regular-svg-icons";

import Comment from "./Comment/index";

import "./style.scss";
import axios from "axios";

import { URL } from "../../constants/url.constant";

let socket;

const Comments = (props) => {
  const { songId, userInfo, onShowComments } = props;
  const [commentsInfo, setCommentsInfo] = useState(null);
  const [comments, setComments] = useState([]);

  useEffect(() => {
    const getComments = async () => {
      const COMMENTS = await axios
        .get(`${URL}/comments/${songId}`)
        .then((res) => res.data);

      if (COMMENTS.isSuccess) {
        setCommentsInfo(COMMENTS.data);
        setComments((comments) => [...comments, ...COMMENTS.data.comments]);
      } else {
        const newComments = await axios
          .post(`${URL}/comments`, {
            song_id: songId,
            sender_id: userInfo._id,
            content: "",
          })
          .then((res) => res.data);
        if (newComments.isSuccess) {
          setCommentsInfo(newComments.data);
        }
      }
    };

    getComments();
  }, []);

  useEffect(() => {
    if (commentsInfo) {
      socket = io(URL, { transports: ["websocket"] });

      socket.emit("joinRoom", { userInfo, song_id: commentsInfo.song_id });
    }
  }, [commentsInfo]);

  useEffect(() => {
    if (socket) {
      socket.on("comment", (data) => {
        setComments((comments) => [
          ...comments,
          {
            sender: {
              id: data.userInfo._id,
              name: `${data.userInfo.firstName} ${data.userInfo.lastName}`,
            },
            content: data.content,
          },
        ]);
      });
    }
  }, [socket]);

  const handleCommentSend = async () => {
    const content = document.getElementById("content").value;
    if (!content) {
      return;
    }

    const updatedComment = await axios
      .put(`${URL}comments/${songId}`, {
        senderId: userInfo._id,
        content,
      })
      .then((res) => res.data);
    if (updatedComment.isSuccess) {
      if (socket) {
        socket.emit("comment", { userInfo, content });
      }
    }

    document.getElementById("content").value = "";
  };

  return (
    <div className="comments--wrapped">
      <div className="comments__info--wrapped">
        <div className="comments__info--wrapped__title">
          <h1>{commentsInfo ? commentsInfo.room.name : ""}</h1>
          <p>{commentsInfo ? commentsInfo.room.artists : ""}</p>
        </div>

        <div className="comments__info--wrapped--close">
          <FontAwesomeIcon
            onClick={() => {
              onShowComments(false);
            }}
            className="comments__info--wrapped--close__icon"
            icon={faTimes}
          />
        </div>
      </div>

      <div className="comments__item--wrapped">
        {comments.length > 0 ? (
          comments.map((comment) => (
            <Comment
              author={comment.sender}
              content={comment.content}
              userId={userInfo._id}
            />
          ))
        ) : (
          <div className="comments__item--wrapped--undefined">
            <FontAwesomeIcon
              className="comments__item--wrapped--undefined__icon"
              icon={faCommentAlt}
            />
            <h1>Chưa có tin nhắn</h1>
            <p>Hãy là người đầu tiên nhắn tin.</p>
          </div>
        )}
      </div>
      <div className="comments__content--wrapped">
        <input
          onKeyPress={(event) => {
            if (event.key === "Enter") {
              handleCommentSend();
            }
          }}
          id="content"
          className="comments__content--wrapped__content"
          type="text"
          placeholder="Aa..."
        />
        <FontAwesomeIcon
          onClick={handleCommentSend}
          className="comments__content--wrapped__button"
          icon={faGooglePlay}
        />
      </div>
    </div>
  );
};

export default Comments;
