import React, { useState, useEffect, useRef } from "react";
import Highcharts from "highcharts";
import axios from "axios";

import Song from "../../components/Main/Song/index";
import WeekChart from "../../components/Main/WeekChart/index";

import "./style.scss";
import { URL } from "../../constants/url.constant";

const TopSongs = React.memo((props) => {
  const { onPlayList } = props;
  const [chart, setChart] = useState({});
  const [list, setList] = useState([]);
  const [highCharts, setHighCharts] = useState({});
  const [weekChart, setWeekChart] = useState({});
  const [listHeight, setListHeight] = useState(0);
  const getIntervalTime = useRef(null);

  useEffect(() => {
    const getList = async () => {
      const topSongs = await axios
        .post(URL + "/topSongs", {
          count: 100,
        })
        .then((res) => res.data);

      if (topSongs.status === "success") {
        setList(topSongs.doc);
      }
    };

    const getChart = async () => {
      const data = await axios.get(URL + "/chart").then((res) => res.data);
      if (data.status === "success") {
        setChart(data.doc.RTChart.chart);
        setWeekChart({
          charts: Object.values(data.doc.weekChart),
          status: "success",
        });
      }
    };

    getList();
    getChart();
  }, []);

  useEffect(() => {
    window.addEventListener("resize", () => {
      handleListWidth(15);
    });
  }, []);

  useEffect(() => {
    handleListWidth(15);
  }, [list]);

  useEffect(() => {
    if (chart.items && list.length > 0) {
      const times = chart.times.map((time) => time.hour);

      let chartData = [];
      let i = 0;
      while (i < 3) {
        const data = chart.items[list[i].id].map((item) => item.counter);
        const color =
          i + 1 === 1
            ? "rgba(220, 38, 38, .8)"
            : i + 1 === 2
            ? "rgba(217, 119, 6)"
            : i + 1 === 3
            ? "rgba(5, 150, 105)"
            : "";

        chartData.push({
          name: list[i].title,
          artist: list[i].artists_names,
          thumbnail: list[i].thumbnail,
          backgroundColor: color,
          color,
          data,
        });
        i++;
      }

      setTimeout(() => {
        Highcharts.chart(
          "chart",
          {
            chart: {
              backgroundColor: "rgb(255, 255, 255, 0)",
            },

            title: {
              text: "",
            },

            xAxis: {
              categories: times,
              lineColor: "gray",
            },

            yAxis: {
              title: {
                text: "",
              },

              labels: {
                style: {
                  opacity: 0,
                },
              },

              gridLineDashStyle: "Dot",
              gridLineColor: "gray",
            },

            series: chartData,

            plotOptions: {
              series: {
                marker: {
                  symbol: "circle",
                  radius: 4,
                },
              },
            },

            legend: {
              enabled: false,
            },

            tooltip: {
              useHTML: true,
              padding: 0,
              borderWidth: 0,
              borderRadius: 4,
              formatter: function () {
                const data = this.series.userOptions;

                return `<div>
                                    <div class="w-50 p-2 rounded flex" style="background: ${data.backgroundColor}">
                                        <img class="h-14 w-14 mr-2 rounded" src="${data.thumbnail}" />
        
                                        <span class="flex flex-col justify-center">
                                            <div class="font-bold text-sm text-white">
                                                ${data.name}<b class="opacity-0">xxxxxxx</b>
                                            </div>
                                            <div class="text-xs text-gray-200">
                                                ${data.artist}<b class="opacity-0">xxxxxxxx</b>
                                            </div>
                                        </span>
                                    </div>
                                </div>`;
              },
            },

            exporting: {
              enabled: false,
            },

            credits: {
              enabled: false,
            },
          },
          function (chart) {
            setHighCharts({
              status: "success",
              chart,
            });
          }
        );
      }, 300);
    }
  }, [chart, list]);

  useEffect(() => {
    if (highCharts.status === "success") {
      handlePointSelected(0);
      let i = 1;
      getIntervalTime.current = setInterval(() => {
        handlePointSelected(i);
        i++;
        if (i > 2) {
          i = 0;
        }
      }, 3000);
    }

    return () => {
      if (getIntervalTime.current) {
        clearInterval(getIntervalTime.current);
      }
    };
  }, [highCharts]);

  const handlePointSelected = (count) => {
    const serie = highCharts.chart.series[count];
    const i = serie.yData.indexOf(serie.dataMax);

    let prevSerie = {};
    let prevIndex = 0;
    if (count > 0) {
      prevSerie = highCharts.chart.series[count - 1];
      prevIndex = prevSerie.yData.indexOf(prevSerie.dataMax);
    } else {
      prevSerie = highCharts.chart.series[highCharts.chart.series.length - 1];
      prevIndex = prevSerie.yData.indexOf(prevSerie.dataMax);
    }

    let nextState = {};
    if (count + 1 >= highCharts.chart.series.length) {
      nextState = highCharts.chart.series[0];
    } else {
      nextState = highCharts.chart.series[count + 1];
    }

    prevSerie.setState("inactive");
    nextState.setState("inactive");
    serie.setState("");

    prevSerie.data[prevIndex].setState();
    serie.data[i].setState("hover");
    highCharts.chart.tooltip.refresh(serie.data[i]);
  };

  const handleListWidth = (count) => {
    const element = document.getElementsByClassName("top-songs__list")[0]
      ? document.getElementsByClassName("top-songs__list")[0].children[0]
      : "";
    if (element) {
      const width = element.clientHeight === 79 ? 80 : element.clientHeight;
      setListHeight(width * count);
    }
  };

  return (
    <div className="w-full h-96 relative top-songs">
      <div className="w-full h-full absolute top-0 top-songs__bg"></div>
      <div className="w-full h-full absolute top-0 bg-gradient-to-br from-sweet-pink via-sweet-pink to-search opacity-90 top-songs__screen"></div>

      <div className="w-full mt-20 absolute top-0 sm:pb-20 pb-14">
        <h1 className="mb-8 sm:mx-6 mx-2 text-4xl font-bold text-pink-400">
          #topsongs
        </h1>

        <div id="chart" className="sm:mx-4 m-0"></div>

        <div
          className="sm:mx-12 mt-4 overflow-hidden duration-300 relative z-10 top-songs__list"
          style={{ height: listHeight }}
        >
          {list.length > 0
            ? list.map((item, index) => (
                <Song
                  key={item.id}
                  position={index + 1}
                  item={item}
                  onPlayList={onPlayList}
                />
              ))
            : ""}
        </div>

        <div className="flex my-4">
          <span
            onClick={() => {
              handleListWidth(100);
            }}
            className="mx-auto mb-4 px-8 py-2 bg-pink-400 hover:opacity-80 text-white rounded-full duration-200 cursor-pointer"
          >
            XEM TOP 100
          </span>
        </div>

        {weekChart.status === "success" ? (
          <div className="w-full relative">
            <div className="h-full w-full absolute top-0 left-0 week-chart__background"></div>
            <div className="h-full w-full absolute top-0 left-0 bg-opacity-90 week-chart__screen"></div>
            <div className="w-full relative z-1">
              <h1 className="mb-8 sm:mx-6 mx-2 pt-2 text-4xl font-bold text-pink-400">
                Bảng Xếp Hạng Tuần
              </h1>
              <div className="px-4 flex xl:flex-row flex-col">
                {weekChart.charts.map((info) => (
                  <WeekChart info={info} onPlayList={onPlayList} />
                ))}
              </div>
            </div>
          </div>
        ) : (
          ""
        )}
      </div>
    </div>
  );
});

export default TopSongs;
