import React from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faPlay} from '@fortawesome/free-solid-svg-icons'

import './style.scss'

const Item = (props) => {
    const { data, width, onPlayList } = props
    
    return(
        <div 
            onClick={()=>{onPlayList([data.encodeId])}}
            className="px-4 list-item" style={{width: width}}
        >
            <div className="relative rounded-xl overflow-hidden cursor-pointer">
                <img className="w-full duration-700" src={data.thumbnail}/>
                <div className="w-full h-full absolute left-0 top-0 items-center justify-center">
                    <span
                        className="sm:h-12 sm:w-12 h-6 w-6 sm:mx-8 mx-4 rounded-full border border-white text-white flex justify-center items-center"
                    >
                        <FontAwesomeIcon 
                            icon={faPlay} 
                            className="sm:text-base text-sm"
                        />
                    </span>
                </div>
            </div>                        
            <p className="text-gray-700 hover:text-gray-900 text-sm font-medium cursor-pointer">{data.title}</p>
            <p className="text-gray-500 hover:text-gray-900 text-xs font-medium cursor-pointer sm:visible invisible">{
                data.artists && data.artists.length > 0 
                ? data.artists.map((artist, index) => index === data.artists.length - 1 ? artist.name : `${artist.name}, `) 
                : ''
            }</p>
        </div>
    )
}

export default Item