import React, { useState } from "react";
import axios from "axios";
import classNames from "classnames";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faAt,
  faLock,
  faUser,
  faMars,
  faVenus,
  faTransgenderAlt,
} from "@fortawesome/free-solid-svg-icons";
import avatar from "../../assets/images/default-avatar.jpg";
import { URL } from "../../constants/url.constant";

const SignIn = (props) => {
  const { onSignUp } = props;
  const [error, setError] = useState({
    type: "",
    msg: "",
  });

  const handleSubmit = async (event) => {
    event.preventDefault();

    const firstName = document.getElementById("first-name").value;
    const lastName = document.getElementById("last-name").value;
    const email = document.getElementById("email").value;
    const password = document.getElementById("password").value;
    const gender = document.querySelector('input[name="gender"]:checked').value;

    if (!firstName) {
      setError({
        type: "warning",
        msg: "Vui lòng nhập họ.",
      });
      return;
    }

    if (!lastName) {
      setError({
        type: "warning",
        msg: "Vui lòng nhập tên.",
      });
      return;
    }

    if (!email) {
      setError({
        type: "warning",
        msg: "Vui lòng nhập email.",
      });
      return;
    }

    if (!password) {
      setError({
        type: "warning",
        msg: "Vui lòng nhập mật khẩu.",
      });
      return;
    }

    if (!gender) {
      setError({
        type: "warning",
        msg: "Vui lòng chọn giới tính.",
      });
      return;
    }

    setError({
      type: "",
      msg: "",
    });

    const user = await axios.post(URL + "/users", {
      type: "signup",
      userInfo: {
        firstName,
        lastName,
        email,
        password,
        gender,
      },
    });

    if (user.data.isSuccess) {
      onSignUp();
    }
  };

  return (
    <form onSubmit={handleSubmit} className="px-4 flex-1">
      <div className="flex flex-col items-center">
        <img className="w-24 h-24 flex-none rounded-full" src={avatar} />
      </div>

      {error.msg.length > 0 ? (
        <div
          className={classNames("mt-2 p-2 italic text-sm text-center rounded", {
            "bg-yellow-500": error.type === "warning",
            "bg-red-500": error.type === "danger",
            "text-white": error.type === "danger",
          })}
        >
          {error.msg}
        </div>
      ) : (
        ""
      )}

      <div className="mt-4 pt-2 pb-1 flex items-center text-gray-500 border-b border-gray-200">
        <FontAwesomeIcon className="ml-2 mr-4 flex-none" icon={faUser} />
        <input
          id="first-name"
          type="text"
          className="mr-2 flex-1 outline-none border-0"
          placeholder="First Name"
        />
      </div>

      <div className="mt-4 pt-2 pb-1 flex items-center text-gray-500 border-b border-gray-200">
        <FontAwesomeIcon className="ml-2 mr-4 flex-none" icon={faUser} />
        <input
          id="last-name"
          type="text"
          className="mr-2 flex-1 outline-none border-0"
          placeholder="Last Name"
        />
      </div>

      <div className="mt-4 pt-2 pb-1 flex items-center text-gray-500 border-b border-gray-200">
        <FontAwesomeIcon className="ml-2 mr-4 flex-none" icon={faAt} />
        <input
          id="email"
          type="email"
          className="mr-2 flex-1 outline-none border-0"
          placeholder="Email"
        />
      </div>

      <div className="mt-4 pt-2 pb-1 flex items-center text-gray-500 border-b border-gray-200">
        <FontAwesomeIcon className="mx-2 mr-4 flex-none" icon={faLock} />
        <input
          id="password"
          type="password"
          className="mr-2 flex-1 outline-none border-0"
          placeholder="Password"
        />
      </div>

      <div className="mt-2 pt-2 pb-1 text-gray-500">
        <label className="ml-2">Giới Tính</label>
        <div className="flex">
          <div className="flex-1 sm:px-2 px-1">
            <div className="sm:p-2 p-1 relative border rounded-xl">
              <FontAwesomeIcon className="mr-2 text-lg" icon={faMars} />
              <span>Male</span>
              <input
                type="radio"
                name="gender"
                value="male"
                className="absolute top-1/2 right-2"
                style={{ transform: "translateY(-50%)" }}
                checked
              />
            </div>
          </div>

          <div className="flex-1 sm:px-2 px-1">
            <div className="sm:p-2 p-1 relative border rounded-xl">
              <FontAwesomeIcon className="mr-2 text-lg" icon={faVenus} />
              <span>Female</span>
              <input
                type="radio"
                name="gender"
                value="female"
                className="absolute top-1/2 right-2"
                style={{ transform: "translateY(-50%)" }}
              />
            </div>
          </div>

          <div className="flex-1 sm:px-2 px-1">
            <div className="sm:p-2 p-1 relative border rounded-xl">
              <FontAwesomeIcon
                className="mr-2 text-lg"
                icon={faTransgenderAlt}
              />
              <span>Option</span>
              <input
                type="radio"
                name="gender"
                value="option"
                className="absolute top-1/2 right-2"
                style={{ transform: "translateY(-50%)" }}
              />
            </div>
          </div>
        </div>
      </div>

      <div className="mb-4 mt-3 border-b border-t border-gray-200">
        <div className="mx-2 my-3 text-gray-400 text-xs">
          Bằng cách nhấp vào{" "}
          <span className="text-blue-400 hover:underline cursor-pointer">
            Đăng ký
          </span>
          , bạn đồng ý với{" "}
          <span className="text-blue-400 hover:underline cursor-pointer">
            Điều khoản
          </span>
          ,{" "}
          <span className="text-blue-400 hover:underline cursor-pointer">
            Chính sách dữ liệu
          </span>{" "}
          và{" "}
          <span className="text-blue-400 hover:underline cursor-pointer">
            Chính sách cookie
          </span>{" "}
          của chúng tôi. Bạn có thể nhận được thông báo của chúng tôi qua SMS và
          hủy nhận bất kỳ lúc nào.
        </div>
      </div>

      <button className="mb-4 py-2 w-full rounded-lg bg-green-500 hover:opacity-80 text-white border-0 duration-200 outline-none focus:outline-none">
        Đăng Ký
      </button>
    </form>
  );
};

export default SignIn;
