import React, { useState } from "react";
import { BrowserRouter as Router } from "react-router-dom";
import classNames from "classnames";
import axios from "axios";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faDoorOpen } from "@fortawesome/free-solid-svg-icons";

import Aside from "./components/Aside";
import Header from "./components/Header/index";
import Main from "./components/Main/index";
import Footer from "./components/Footer/index";
import SignIn from "./components/SignIn/index";
import SignUp from "./components/SignUp/index";
import Comments from "./components/Comments/index";

import { URL } from "./constants/url.constant";
import "./App.scss";

const App = React.memo(() => {
  const [playList, setPlayList] = useState([]);
  const [isTop, setIsTop] = useState(true);
  const [showUserForm, setShowUserForm] = useState(false);
  const [userForm, setUserForm] = useState("signin");
  const [userInfo, setUserInfo] = useState(null);
  const [isFavorite, setIsFavorite] = useState(false);
  const [songId, setSongId] = useState(null);
  const [isShowComments, setIsShowComments] = useState(false);

  const handleUserPlaylist__play = (list) => {
    setPlayList(userInfo.playlist);
  };

  const handlePlayList = async (id) => {
    let list = [];

    if (typeof id === "object") {
      const playList__info = await axios
        .get(`${URL}/playList/${id[0]}`)
        .then((res) => res.data);
      if (playList__info.status === "success") {
        if (playList__info.doc.streamingStatus === 2) {
          return;
        }

        list = playList__info.doc.map((item) =>
          item.streamingStatus === 2 ? "VIP" : item.encodeId
        );
      }
    }

    if (typeof id === "string") {
      list = [id];
    }

    setPlayList(list);
  };

  const handleScroll = (event) => {
    const scrollTop = event.target.scrollTop;
    if (scrollTop === 0) {
      return setIsTop(true);
    }
    setIsTop(false);
  };

  const handleUserForm = (type) => {
    setUserForm(type);
  };

  const handleUserForm__visible = () => {
    if (!userInfo) {
      setShowUserForm(true);
      setUserForm("signin");
      return;
    }
  };

  const handleUserForm__hidden = () => {
    if (window.location.pathname === "/user") {
      window.location.pathname = "/";
      return;
    }
    setShowUserForm(false);
    setUserForm("signin");
  };

  const handleUserInfo = (userInfo) => {
    setUserInfo(userInfo);
  };

  const handleFavorite = (id) => {
    if (userInfo && userInfo.playlist.indexOf(id) !== -1) {
      setIsFavorite(true);
      return;
    }

    setIsFavorite(false);
  };

  const handleUserPlayList = (bool) => {
    if (userInfo) {
      setIsFavorite(bool);
    }
  };

  const handleUserPlayList__update = async (id, bool) => {
    if (!userInfo) {
      handleUserForm__visible();
      return;
    }

    const url = `${URL}/users/${userInfo._id}`;
    const user = await axios
      .put(url, {
        isAdd: bool,
        songId: id,
      })
      .then((res) => res.data);

    if (bool) {
      setIsFavorite(bool);
    }

    if (user.isSuccess) {
      setUserInfo(user.data);
      return;
    }
  };

  const handleCommentsById = (id = null) => {
    setSongId(id);
    setIsShowComments(true);
  };

  const handleShowComments = (bool) => {
    setIsShowComments(bool);
  };

  return (
    <Router>
      <div className="h-screen flex bg-sweet-pink overflow-hidden">
        <div className="w-screen flex">
          <Header
            onPlayList={handlePlayList}
            onTop={isTop}
            onUserForm={handleUserForm__visible}
          />
          <Aside />

          <div
            onScroll={handleScroll}
            className="sm:ml-18 md:ml-64 flex-1 flex duration-300 overflow-auto"
          >
            <Main
              onUserPlaylist__play={handleUserPlaylist__play}
              onPlayList={handlePlayList}
              userInfo={userInfo}
              onUserForm__visible={handleUserForm__visible}
            />
          </div>
        </div>

        <Footer
          playList={playList}
          onFavorite={handleFavorite}
          onUserPlaylist={handleUserPlayList}
          onUserPlaylist__update={handleUserPlayList__update}
          isFavorite={isFavorite}
          onCommentsById={handleCommentsById}
        />

        {!showUserForm ? (
          ""
        ) : (
          <div className="flex flex-col absolute top-1/2 left-1/2 bg-white rounded-xl shadow-lg z-50 form">
            <div className="mb-4 font-bold text-base text-gray-700 text-center flex">
              <div
                onClick={() => handleUserForm("signin")}
                className="py-4 flex-1 border-r border-b relative cursor-pointer"
              >
                <span
                  className={classNames("duration-300", {
                    "text-gray-400": userForm !== "signin",
                  })}
                >
                  Đăng Nhập
                </span>
                <span
                  className={classNames(
                    "h-0.5 w-32 absolute bottom-0 left-1/2 bg-gray-700 duration-300",
                    { "opacity-0": userForm !== "signin" }
                  )}
                  style={{ transform: "translateX(-50%)" }}
                ></span>
              </div>

              <div
                onClick={() => handleUserForm("signup")}
                className="py-4 flex-1 border-b relative cursor-pointer"
              >
                <span
                  className={classNames("duration-300", {
                    "text-gray-400": userForm !== "signup",
                  })}
                >
                  Đăng Ký
                </span>
                <span
                  className={classNames(
                    "h-0.5 w-32 absolute bottom-0 left-1/2 bg-gray-700 duration-300",
                    { "opacity-0": userForm !== "signup" }
                  )}
                  style={{ transform: "translateX(-50%)" }}
                ></span>
              </div>
            </div>

            {userForm === "signin" ? (
              <SignIn
                onSignIn={handleUserForm__hidden}
                onUserInfo={handleUserInfo}
              />
            ) : (
              <SignUp onSignUp={handleUserForm__visible} />
            )}

            <button
              onClick={handleUserForm__hidden}
              className="mx-4 mb-4 py-2 text-gray-700 rounded-lg hover:opacity-80 border-2 duration-200 outline-none focus:outline-none"
            >
              <FontAwesomeIcon className="mr-2" icon={faDoorOpen} />
              Quay lại
            </button>
          </div>
        )}

        {showUserForm === false ? (
          ""
        ) : (
          <div
            onClick={handleUserForm__hidden}
            className="w-full h-full absolute top-0 left-0 bg-white bg-opacity-80 z-40 cursor-pointer"
          ></div>
        )}
      </div>

      {songId && userInfo && isShowComments ? (
        <Comments
          userInfo={userInfo}
          songId={songId}
          onShowComments={handleShowComments}
        />
      ) : (
        ""
      )}
    </Router>
  );
});
export default App;
