import React, { useState, useEffect } from "react";
import axios from "axios";

import PlayList from "../../components/Main/PlayList/index";

import logo from "../../assets/images/top100.png";
import "./style.scss";
import { URL } from "../../constants/url.constant";

const Top100 = (props) => {
  const { onPlayList } = props;
  const [playlists, setPlayLists] = useState([]);

  useEffect(() => {
    const getTop100 = async () => {
      const data = await axios.get(URL + "/playlist").then((res) => res.data);
      if (data.status === "success") {
        setPlayLists(data.doc);
      }
    };

    getTop100();
  }, []);

  return (
    <div className="w-full h-72 relative">
      <div className="w-full h-full absolute top-0 top-100__bg"></div>
      <div className="w-full h-full absolute top-0 bg-gradient-to-br from-sweet-pink via-sweet-pink to-search opacity-90 top-100__screen"></div>

      <div className="w-full mt-20 absolute top-0 sm:pb-24 pb-14">
        <div className="flex">
          <img className="mx-auto h-40" src={logo} />
        </div>

        <div className="sm:mx-12 mt-4 overflow-hidden duration-300">
          {playlists.length > 0
            ? playlists.map((list, index) => (
                <PlayList
                  key={list.genre.name}
                  index={index}
                  list={list}
                  onPlayList={onPlayList}
                />
              ))
            : ""}
        </div>
      </div>
    </div>
  );
};

export default Top100;
