import React, { useState, useEffect, useRef } from "react";
import Highcharts from "highcharts";

import TopSong from "./TopSong/index";

import "./style.scss";

const Chart = (props) => {
  const { chart, topSongItems, onPlayList } = props;
  const [count, setCount] = useState(0);
  const [highCharts, setHighCharts] = useState({});
  const getIntervalTime = useRef(null);

  useEffect(() => {
    if (highCharts.status === "success") {
      handleRankSelected__onAuto();
    }

    return handleIntervalTime;
  }, [highCharts]);

  useEffect(() => {
    if (highCharts.status === "success") {
      handleRankSelected();
      handlePointSelected();
    }
  }, [count || highCharts]);

  //SETUP CHART
  useEffect(() => {
    const times = chart.times.map((time) => time.hour);

    let chartData = [];
    let i = 0;
    while (i < topSongItems.length) {
      const data = chart.items[topSongItems[i].id].map((item) => item.counter);
      const color =
        topSongItems[i].position === 1
          ? "rgba(220, 38, 38, .8)"
          : topSongItems[i].position === 2
          ? "rgba(217, 119, 6)"
          : topSongItems[i].position === 3
          ? "rgba(5, 150, 105)"
          : "";

      chartData.push({
        name: topSongItems[i].title,
        artist: topSongItems[i].artists_names,
        thumbnail: topSongItems[i].thumbnail,
        percent: `${Math.round(
          (topSongItems[i].total / chart.totalScore) * 100
        )}%`,
        backgroundColor: color,
        color,
        data,
      });
      i++;
    }

    setTimeout(() => {
      Highcharts.chart(
        "chart",
        {
          chart: {
            backgroundColor: "rgb(255, 255, 255, 0)",
          },

          title: {
            text: "",
          },

          xAxis: {
            categories: times,
            lineColor: "gray",
            labels: {
              style: {
                color: "white",
              },
            },
          },

          yAxis: {
            title: {
              text: "",
            },

            labels: {
              style: {
                opacity: 0,
              },
            },

            gridLineDashStyle: "Dot",
            gridLineColor: "gray",
          },

          series: chartData,

          plotOptions: {
            series: {
              marker: {
                symbol: "circle",
                radius: 4,
              },
            },
          },

          legend: {
            enabled: false,
          },

          tooltip: {
            useHTML: true,
            padding: 0,
            borderWidth: 0,
            borderRadius: 4,
            formatter: function () {
              const data = this.series.userOptions;

              return `<div>
                                <div class="w-50 p-2 rounded flex" style="background: ${data.backgroundColor}">
                                    <img class="h-14 w-14 mr-2 rounded" src="${data.thumbnail}" />
    
                                    <span class="flex flex-col justify-center">
                                        <div class="font-bold text-sm text-white">
                                            ${data.name}<b class="opacity-0">xxxxxxx</b>
                                        </div>
                                        <div class="text-xs text-gray-200">
                                            ${data.artist}<b class="opacity-0">xxxxxxxx</b>
                                        </div>
                                    </span>
                                </div>
                            </div>`;
            },
          },

          exporting: {
            enabled: false,
          },

          credits: {
            enabled: false,
          },
        },
        function (chart) {
          setHighCharts({ chart, status: "success" });
        }
      );
    }, 300);
  }, []);

  const handleRankSelected = (newCount = count) => {
    const ranks = document.getElementsByClassName("rank");
    const rankSelected = document.getElementsByClassName("rank__selected")[0];

    if (rankSelected) {
      rankSelected.classList.remove("rank__selected");
    }

    if (ranks.length > 0) {
      ranks[newCount].classList.add("rank__selected");
    }
  };

  const handleRankSelected__onAuto = (newCount = count) => {
    getIntervalTime.current = setInterval(() => {
      handleRankSelected(newCount);
      if (highCharts.status === "success") {
        handlePointSelected(newCount);
      }

      newCount += 1;

      if (newCount > 2) {
        newCount = 0;
      }

      handleCount(newCount);
    }, 3000);
  };

  const handleRankSelected_onMouseOver = () => {
    handleIntervalTime();
  };

  const handleRankSelected_onMouseOut = (i = count) => {
    handleRankSelected__onAuto(i);
  };

  const handlePointSelected = (newCount = count) => {
    const serie = highCharts.chart.series[newCount];
    const i = serie.yData.indexOf(serie.dataMax);

    let prevSerie = {};
    let prevIndex = 0;
    if (newCount > 0) {
      prevSerie = highCharts.chart.series[newCount - 1];
      prevIndex = prevSerie.yData.indexOf(prevSerie.dataMax);
    } else {
      prevSerie = highCharts.chart.series[highCharts.chart.series.length - 1];
      prevIndex = prevSerie.yData.indexOf(prevSerie.dataMax);
    }

    let nextState = {};
    if (newCount + 1 >= highCharts.chart.series.length) {
      nextState = highCharts.chart.series[0];
    } else {
      nextState = highCharts.chart.series[newCount + 1];
    }

    prevSerie.setState("inactive");
    nextState.setState("inactive");
    serie.setState("");

    prevSerie.data[prevIndex].setState();
    serie.data[i].setState("hover");
    highCharts.chart.tooltip.refresh(serie.data[i]);
  };

  const handleCount = (newCount) => {
    setCount(newCount);
  };

  const handleIntervalTime = () => {
    if (getIntervalTime.current) {
      clearInterval(getIntervalTime.current);
    }
  };

  return (
    <div className="sm:mb-4 sm:mt-8 mt-6 relative">
      <div className="w-full h-full absolute sm:rounded-xl chart__bg"></div>
      <div className="w-full h-full absolute sm:rounded-xl opacity-90 chart__screen"></div>

      <div className="relative z-10">
        <p className="py-3 px-4 text-3xl font-bold text-white">#topsongs</p>
        <div className="flex xl:flex-row flex-col-reverse">
          <div className="sm:p-4 py-4 flex-1 flex flex-col">
            <div
              onMouseEnter={() => {
                handleRankSelected_onMouseOver();
              }}
              onMouseLeave={() => {
                handleRankSelected_onMouseOut();
              }}
              className="flex flex-col"
            >
              {chart.totalScore > 0
                ? topSongItems.map((item) => (
                    <TopSong
                      key={item.id}
                      item={item}
                      totalScore={chart.totalScore}
                      onPlayList={onPlayList}
                      onCount={handleCount}
                    />
                  ))
                : ""}
            </div>

            <div className="my-4 flex justify-center">
              <button
                onClick={() => {
                  window.location.pathname = "/top-songs";
                }}
                className="py-2 px-4 bg-indigo-600 hover:opacity-80 rounded-full text-white duration-200 cursor-pointer"
                to="/top-songs"
              >
                Xem Top 100
              </button>
            </div>
          </div>

          <div id="chart" className="sm:px-8 xl:pb-8 p-0 flex-2"></div>
        </div>
      </div>
    </div>
  );
};

export default Chart;
