import React, { Component } from "react";

export default class ResultItem extends Component {
  render() {
    const { itemInfo, onPlayList, onResult, onValueDelete } = this.props;

    const handleResultClick = () => {
      const id = itemInfo.radioPid ? [itemInfo.radioPid] : itemInfo.id;
      onPlayList(id);
      onResult();
      onValueDelete();
    };

    console.log(itemInfo);

    return (
      <li
        onClick={handleResultClick}
        className="h-16 py-2 px-2 flex items-center hover:bg-egg rounded-xl truncate cursor-pointer duration-200"
      >
        <img
          className="h-12 sm:mr-4 mr-2 rounded-md"
          src={
            "https://photo-resize-zmp3.zadn.vn/w94_r1x1_jpeg/" + itemInfo.thumb
          }
        />

        <div className="flex flex-col">
          <div>{itemInfo.name}</div>
          <div className="text-sm text-gray-500">{itemInfo.artist}</div>
        </div>
      </li>
    );
  }
}
