import React, { useState, useEffect } from "react";
import ReactPlayer from "react-player";
import classNames from "classnames";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import axios from "axios";

import {
  faPlay,
  faPause,
  faStepBackward,
  faStepForward,
  faVolumeDown,
  faVolumeMute,
  faHeart as fasHeart,
} from "@fortawesome/free-solid-svg-icons";

import {
  faHeart as farHeart,
  faComment,
} from "@fortawesome/free-regular-svg-icons";

import "./style.scss";
import { URL } from "../../constants/url.constant";

const Footer = (props) => {
  const {
    playList,
    onFavorite,
    onUserPlaylist__update,
    isFavorite,
    onUserPlaylist,
    onCommentsById,
  } = props;
  const [loop, setLoop] = useState(true);
  const [isPlaying, setIsPlaying] = useState(false);
  const [volume, setVolume] = useState(50);
  const [duration, setDuration] = useState(0);
  const [playedSeconds, setPlayedSeconds] = useState(0);
  const [audioInfo, setAudioInfo] = useState({
    index: 0,
    id: "ZOAC7BUF",
    url: "http://api.mp3.zing.vn/api/streaming/audio/ZOAC7BUF/320",
    artistsNames: "Binz, Đen",
    title: "Cho Mình Em",
    thumbnail:
      "https://photo-resize-zmp3.zadn.vn/w240_r1x1_jpeg/cover/7/9/3/c/793cb0dc6bf49c1640db7726dcab3504.jpg",
  });
  const [player, setPlayer] = useState({});

  useEffect(() => {
    handleProgressBar();
  }, [playedSeconds]);

  useEffect(() => {
    handleVolumeBar();
  }, [volume]);

  useEffect(() => {
    if (playList.length > 0) {
      setLoop(false);
      handleAudioInfo(0);
    }
  }, [playList]);

  const handlePlaying = () => {
    setIsPlaying(!isPlaying);
  };

  const handleDuration = (duration) => {
    setDuration(duration);
  };

  const handlePlayedSeconds = (progress) => {
    const played = progress.playedSeconds;

    document.getElementById("progress-bar__slider").value = played;

    setPlayedSeconds(played);
  };

  const handleProgressBar = () => {
    const bar = document.getElementById("progress-bar");
    const w_parent = bar.parentElement.offsetWidth;
    const radio = (playedSeconds / duration) * 100;
    bar.style.width = `${(w_parent * radio) / 100}px`;
  };

  const handleProgressBarSlider = () => {
    const played = document.getElementById("progress-bar__slider").value;

    setIsPlaying(false);
    player.seekTo(played);
    setPlayedSeconds(played);
  };

  const handleVolumeBar = () => {
    const bar = document.getElementById("volume-bar");
    const w_parent = bar.parentElement.offsetWidth;
    bar.style.width = `${(w_parent * volume) / 100}px`;
  };

  const handleVolumeButton = () => {
    let volumeOption = volume;
    volumeOption = volumeOption > 0 ? 0 : 100;

    setVolume(volumeOption);
  };

  const handleVolumeBarSlider = () => {
    const volumeOption = document.getElementById("volume-bar__slider").value;
    setVolume(volumeOption);
  };

  const handleAudioInfo = async (index) => {
    if (playList[index] === "VIP") {
      if (playList[index + 1]) {
        handleAudioInfo(index + 1);
        return;
      }

      handleAudioInfo(0);
      return;
    }

    if (index < 0) {
      index = playList.length - 1;
    }

    if (index >= playList.length) {
      index = 0;
    }

    const audio = await axios
      .get(URL + `/mp3/${playList[index]}`)
      .then((res) => res.data);
    if (audio.status === "success") {
      const { encodeId, artistsNames, title, thumbnail } = audio.doc;
      onFavorite(encodeId);
      setAudioInfo({
        index,
        id: encodeId,
        url: `http://api.mp3.zing.vn/api/streaming/audio/${encodeId}/320`,
        artistsNames,
        title,
        thumbnail,
      });

      setIsPlaying(true);
    }
  };

  const handleEnded = () => {
    setIsPlaying(false);
    handleAudioInfo(audioInfo.index + 1);
  };

  const ref = (player) => {
    setPlayer(player);
  };

  return (
    <footer className="sm:h-20 h-12 w-screen sm:px-5 px-2 fixed bottom-0 bg-audio border border-pink-100 shadow-lg z-30">
      <ReactPlayer
        url={
          audioInfo.url.length > 0
            ? audioInfo.url
            : "http://api.mp3.zing.vn/api/streaming/audio/ZOAC7BUF/320"
        }
        loop={loop}
        playing={isPlaying}
        volume={volume / 100}
        ref={ref}
        onDuration={handleDuration}
        onProgress={handlePlayedSeconds}
        onEnded={handleEnded}
        className="hidden"
      />

      <div className="h-full flex items-center">
        <div className="sm:flex-1 flex-2 flex">
          <img
            className={classNames(
              "sm:h-14 sm:w-14 h-10 w-10 mr-2 rounded-full border-2 border-white cursor-pointer",
              { "animate-spin-slow": isPlaying }
            )}
            src={
              audioInfo.thumbnail.length > 0
                ? audioInfo.thumbnail
                : "https://photo-resize-zmp3.zadn.vn/w240_r1x1_jpeg/cover/7/9/3/c/793cb0dc6bf49c1640db7726dcab3504.jpg"
            }
          />

          <span className="mr-4 flex flex-col justify-center truncate">
            <div className="text-sm font-bold text-gray-700 cursor-pointer">
              {audioInfo.title.length > 0 ? audioInfo.title : "Cho Mình Em"}
            </div>
            <div className="text-xs text-gray-600 cursor-pointer">
              {audioInfo.artistsNames.length > 0
                ? audioInfo.artistsNames
                : "Binz, Đen"}
            </div>
          </span>

          <span className="mr-4 flex items-center">
            {isFavorite ? (
              <FontAwesomeIcon
                onClick={() => {
                  onUserPlaylist__update(audioInfo.id, false);
                  onUserPlaylist(false);
                }}
                className="text-pink-400 cursor-pointer"
                icon={fasHeart}
              />
            ) : (
              <FontAwesomeIcon
                onClick={() => {
                  onUserPlaylist__update(audioInfo.id, true);
                }}
                className="text-gray-700 cursor-pointer"
                icon={farHeart}
              />
            )}
          </span>

          <span className="flex items-center">
            <FontAwesomeIcon
              onClick={() => {
                console.log("comment");
                onCommentsById(audioInfo.id);
              }}
              className="text-gray-700 cursor-pointer"
              icon={faComment}
            />
          </span>
        </div>

        <div className="sm:flex-2 flex-1 flex flex-col">
          <div className="flex-2 flex items-center sm:justify-center justify-end">
            <FontAwesomeIcon
              onClick={() => {
                handleAudioInfo(+audioInfo.index - 1);
              }}
              icon={faStepBackward}
              className="sm:text-base text-sm text-gray-700 hover:text-gray-600 cursor-pointer"
            />

            <span
              onClick={handlePlaying}
              className="sm:h-10 sm:w-10 h-6 w-6 sm:mx-8 mx-4 rounded-full border border-gray-700 hover:border-pink-500 text-gray-700 hover:text-pink-500 hover:text-gray-600 flex justify-center items-center duration-100 cursor-pointer"
            >
              <FontAwesomeIcon
                icon={isPlaying ? faPause : faPlay}
                className="sm:text-base text-xs"
              />
            </span>

            <FontAwesomeIcon
              onClick={() => {
                handleAudioInfo(+audioInfo.index + 1);
              }}
              icon={faStepForward}
              className="sm:text-base text-sm text-gray-700 hover:text-gray-600 cursor-pointer"
            />
          </div>
          <div className="sm:w-auto w-0 flex-1 flex items-center">
            <div className="sm:inline hidden flex-1 text-right text-sm text-gray-500">
              {`${parseInt(playedSeconds / 60)}`.length < 2
                ? `0${parseInt(playedSeconds / 60)}`
                : parseInt(playedSeconds / 60)}
              :
              {`${parseInt(Math.round(playedSeconds % 60))}`.length < 2
                ? `0${parseInt(Math.round(playedSeconds % 60))}`
                : parseInt(Math.round(playedSeconds % 60))}
            </div>

            <div className="h-1 w-full sm:mx-4 mx-0 sm:relative absolute top-0 left-0 flex-2 bg-gray-500 bg-opacity-20 sm:rounded-full duration-200 progress-bar__wrapped">
              <div
                id="progress-bar"
                className="h-full w-0 bg-pink-400 rounded-full"
              ></div>
              <input
                onInput={handleProgressBarSlider}
                onMouseUp={() => {
                  setIsPlaying(true);
                }}
                id="progress-bar__slider"
                min={0}
                max={duration}
                type="range"
                className="h-full w-full absolute top-0 left-0 bg-transparent outline-none progress-bar__slider"
              />
            </div>

            <div className="sm:inline hidden flex-1 text-left text-sm text-gray-700">
              {`${parseInt(duration / 60)}`.length < 2
                ? `0${parseInt(duration / 60)}`
                : parseInt(duration / 60)}
              :
              {`${parseInt(Math.round(duration % 60))}`.length < 2
                ? `0${parseInt(Math.round(duration % 60))}`
                : parseInt(Math.round(duration % 60))}
            </div>
          </div>
        </div>

        <div className="flex-1 md:flex hidden justify-end items-center">
          <FontAwesomeIcon
            onClick={handleVolumeButton}
            icon={volume > 0 ? faVolumeDown : faVolumeMute}
            className="mr-4 text-lg text-gray-700 cursor-pointer"
          />
          <div className="w-20 h-1 bg-gray-500 bg-opacity-20 relative">
            <div
              id="volume-bar"
              className="h-full w-1/2 bg-pink-400 rounded-full"
            ></div>
            <input
              onInput={handleVolumeBarSlider}
              id="volume-bar__slider"
              min={0}
              max={100}
              value={volume}
              type="range"
              className="h-full w-full absolute top-0 left-0 bg-transparent outline-none volume-bar__slider"
            />
          </div>
        </div>
      </div>
    </footer>
  );
};

export default Footer;
