import React, { useState, useRef } from "react";
import onClickOutside from "react-onclickoutside";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faSearch, faTimes } from "@fortawesome/free-solid-svg-icons";
import classNames from "classnames";
import axios from "axios";

import ResultItem from "./ResultItem/index";

import { URL } from "../../../constants/url.constant";

function Search(props) {
  const { onTop, onPlayList } = props;
  const [isShowDelBtn, setIsShowDelBtn] = useState(false);
  const [hasResult, setHasResult] = useState(false);
  const [top, setTop] = useState(null);
  const [song, setSong] = useState(null);
  const typingTimeoutRef = useRef(null);

  Search.handleClickOutside = () => {
    setHasResult(false);
  };

  function handleCallAPI(query) {
    if (typingTimeoutRef.current) {
      clearTimeout(typingTimeoutRef.current);
    }

    typingTimeoutRef.current = setTimeout(async () => {
      const data = await axios
        .post(URL + `/mp3`, {
          query,
        })
        .then((res) => res.data);

      if (data.status === "success") {
        if (data.doc[0]) {
          setTop(data.doc[0].top);
          setSong(data.doc[0].song);
        }

        if (data.doc[1]) {
          setSong(data.doc[1].song);
        }

        setHasResult(true);
      }
    }, 300);
  }

  function handleValueChange(event) {
    const query = event.target.value;
    if (!query) {
      setIsShowDelBtn(false);
      setHasResult(false);
      setTop({});
      setSong([]);
      return;
    }

    setIsShowDelBtn(true);
    handleCallAPI(query);
  }

  function handleValueFocus(event) {
    const query = event.target.value;
    if (!query) {
      return;
    }

    handleCallAPI(query);
  }

  function handleValueDelete() {
    document.getElementById("search").value = "";
    setHasResult(false);
  }

  return (
    <div
      className={classNames(
        "max-w-xl md:w-2/3 w-full h-10 lg:mx-8 mx-4 text-gray-700 rounded-t-3xl",
        {
          "rounded-3xl": !hasResult,
          "bg-opacity-70": onTop,
        }
      )}
      style={{ backgroundColor: !hasResult ? "rgba(0,0,0,0.05)" : "white" }}
    >
      <div className="flex relative py-2 sm:px-8 px-4">
        <FontAwesomeIcon
          className="absolute top-1/2 sm:left-4 sm:text-lg left-2 text-sm cursor-pointer"
          icon={faSearch}
          style={{ transform: "translateY(-50%)" }}
        />

        <input
          onChange={handleValueChange}
          onFocus={handleValueFocus}
          id="search"
          className="mx-3 flex-1 bg-transparent border-transparent sm:text-md text-sm outline-none"
          type="text"
          placeholder="Nhập tên bài hát, ca sĩ, ..."
          autoComplete="off"
        />

        {isShowDelBtn ? (
          <FontAwesomeIcon
            onClick={handleValueDelete}
            className="absolute top-1/2  sm:right-4 sm:text-lg right-2 text-sm  cursor-pointer"
            icon={faTimes}
            style={{ transform: "translateY(-50%)" }}
          />
        ) : (
          ""
        )}
      </div>

      {hasResult && top ? (
        <div className="px-4 py-2 bg-white shadow">
          <p className="text-lg text-gray-600">Top Kết Quả</p>
          <ul>
            <ResultItem
              itemInfo={top}
              onPlayList={onPlayList}
              onResult={Search.handleClickOutside}
              onValueDelete={handleValueDelete}
            />
          </ul>
        </div>
      ) : (
        ""
      )}

      {hasResult && song ? (
        <div className="px-4 py-2 sm:rounded-b-3xl bg-white shadow">
          <p className="text-lg text-gray-600">Bài Hát</p>
          <ul>
            {song.map((itemInfo) => (
              <ResultItem
                key={itemInfo.id}
                itemInfo={itemInfo}
                onPlayList={onPlayList}
                onResult={Search.handleClickOutside}
                onValueDelete={handleValueDelete}
              />
            ))}
          </ul>
        </div>
      ) : (
        ""
      )}
    </div>
  );
}

const clickOutsideConfig = {
  handleClickOutside: () => Search.handleClickOutside,
};

export default onClickOutside(Search, clickOutsideConfig);
