import React from 'react'
import TopSong from '../Song/index'

const WeekChart = (props) => {
    const { info, onPlayList } = props

    return(
        <div className="flex-1 pb-5 px-3 overflow-hidden week-chart">
            <div className="bg-white bg-opacity-50 rounded-2xl">
                <h2 className="pl-8 pt-4 pb-2 text-2xl font-bold text-gray-700">
                    {info.country === 'vn' ? 'Việt Nam' : info.country === 'korea' ? 'Korea' : info.country === 'us' ? 'US - UK' : ''}
                </h2>
                <div className="p-2">
                    {info.items.map((item, index) => {
                        if(index < 5){
                            return <TopSong key={item.encodeId} position={index + 1} item={item} onPlayList={onPlayList} isWeekChart={true}/>
                        }
                    })}
                </div>
            </div>
        </div>
    )
}

export default WeekChart