import React, { Component } from "react";
import NewSongItem from "./NewSongItem/index";

export default class NewSong extends Component {
  render() {
    const { items, onPlayList } = this.props;

    return (
      <div className="sm:rounded-xl">
        <h1 className="sm:p-4 sm:pb-0 px-2 pt-4 pb-0 text-lg text-gray-700 font-bold capitalize">
          Mới Phát Hành
        </h1>
        <div className="sm:p-2 sm:pt-0 p-0 flex sm:flex-row flex-col overflow-hidden">
          {items.length > 0 ? (
            <ul className="flex-1 flex flex-col">
              {items.map((item, index) => {
                if (index >= 0 && index < 5)
                  return (
                    <NewSongItem
                      key={item.encodeId}
                      item={item}
                      onPlayList={onPlayList}
                    />
                  );
              })}
            </ul>
          ) : (
            ""
          )}
          {items.length > 5 ? (
            <ul className="flex-1 flex flex-col">
              {items.map((item, index) => {
                if (index >= 5 && index < 10)
                  return (
                    <NewSongItem
                      key={item.encodeId}
                      item={item}
                      onPlayList={onPlayList}
                    />
                  );
              })}
            </ul>
          ) : (
            ""
          )}
          {items.length > 10 ? (
            <ul className="flex-1 flex flex-col lg:inline hidden">
              {items.map((item, index) => {
                if (index >= 10 && index < 15)
                  return (
                    <NewSongItem
                      key={item.encodeId}
                      item={item}
                      onPlayList={onPlayList}
                    />
                  );
              })}
            </ul>
          ) : (
            ""
          )}
        </div>
      </div>
    );
  }
}
