import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

import "./style.scss";

const Item = (props) => {
  const { data, width, onPlayList } = props;

  return (
    <div
      onClick={() => {
        console.log(data.encodeId);
      }}
      className="hub-list__item"
      style={{ width }}
    >
      <div className="hub-list__item__image--wrapped">
        <img className="hub-list__item__image" src={data.thumbnail} />

        <div className="hub-list__item-info">
          <h1 className="title">{data.title}</h1>
          <div className="thumb-images">
            {data.playlists && data.playlists.length > 0
              ? data.playlists.map((playlist) => (
                  <img
                    key={playlist.encodeId}
                    className="image"
                    src={playlist.thumbnail}
                  />
                ))
              : ""}
          </div>
        </div>
      </div>
    </div>
  );
};

export default Item;
