import React, { useState } from "react";
import classNames from "classnames";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faPlay,
  faWindowMinimize,
  faCaretUp,
  faCaretDown,
} from "@fortawesome/free-solid-svg-icons";

import vipLabel from "../../../assets/images/vip-label.svg";
import "./style.scss";

const TopSong = React.memo((props) => {
  const { item, position, onPlayList, isWeekChart } = props;
  const [isVIP, setIsVIP] = useState(item.streamingStatus === 2);
  const rankStatus =
    item.rank_status === "stand"
      ? 0
      : item.rank_status === "up"
      ? item.rank_num
      : item.rank_status === "down"
      ? -item.rank_num
      : item.rakingStatus;

  const handleClick = () => {
    if (item.streamingStatus === 2) {
      return;
    }

    const id = item.playlist_id
      ? [item.playlist_id]
      : item.id
      ? item.id
      : item.encodeId;
    onPlayList(id);
  };

  return (
    <div
      onClick={handleClick}
      className={classNames(
        "sm:p-3 p-2 flex hover:bg-opacity-50 border-gray-400 sm:rounded-xl cursor-pointer duration-200 rank",
        {
          rank__selected: position === 1,
          "hover:bg-white": !isWeekChart,
          "hover:bg-audio": isWeekChart,
        }
      )}
    >
      {position ? (
        <span
          className={classNames(
            "flex items-center justify-center flex-shrink-0 top-rank",
            {
              "top-1": position === 1,
              "top-2": position === 2,
              "top-3": position === 3,
              "top-else": position > 3,
              "w-10": !isWeekChart,
            }
          )}
        >
          {position}
        </span>
      ) : (
        ""
      )}

      {position ? (
        <div className="w-4 sm:mx-4 mx-4 flex flex-col justify-center items-center font-bold">
          <FontAwesomeIcon
            className={classNames("text-base", {
              "text-red-500": rankStatus < 0,
              "text-green-500": rankStatus > 0,
              "text-gray-500": rankStatus === 0,
              "mb-3": rankStatus === 0,
            })}
            icon={
              rankStatus === 0
                ? faWindowMinimize
                : rankStatus > 0
                ? faCaretUp
                : faCaretDown
            }
          />
          <p
            className={classNames("text-sm", {
              "text-red-500": rankStatus < 0,
              "text-green-500": rankStatus > 0,
              hidden: rankStatus === 0,
            })}
          >
            {rankStatus > 0 ? rankStatus : -rankStatus}
          </p>
        </div>
      ) : (
        ""
      )}

      <span className="w-14 mr-2 relative rounded overflow-hidden flex-shrink-0">
        <img
          className="w-14 duration-300 top-song__thumbnail"
          src={item.thumbnail}
        />
        <span className="w-full h-full absolute top-0 left-0 justify-center items-center top-song__screen">
          <FontAwesomeIcon className="text-white text-lg" icon={faPlay} />
        </span>
      </span>

      <span className="py-2 overflow-hidden">
        <div className="flex">
          <p
            className={classNames(
              "text-sm text-gray-400 font-medium truncate",
              { "text-gray-700": !isVIP }
            )}
          >
            {item.title}
          </p>

          {isVIP ? <img className="ml-2" src={vipLabel} /> : ""}
        </div>
        <div
          className={classNames("text-xs text-gray-400 truncate", {
            "text-gray-500": !isVIP,
          })}
        >
          {item.artists_names ? item.artists_names : item.artistsNames}
        </div>
      </span>
    </div>
  );
});

export default TopSong;
