import React, { useEffect, useState, useRef } from "react";

import "./style.scss";

const SliderNews = (props) => {
  const { items } = props;
  const [childrenWidth, setChildrenWidth] = useState(46.5);
  const getIntervalTime = useRef(null);

  //HANDLE SLIDE SIZE
  useEffect(() => {
    const handleResize = () => {
      const windowWidth = window.innerWidth;

      if (windowWidth >= 1107) {
        setChildrenWidth(46.5);
      } else if (windowWidth >= 900) {
        setChildrenWidth(60);
      } else {
        setChildrenWidth(100);
      }
    };

    window.addEventListener("resize", handleResize);
    handleResize();

    return () => {
      window.removeEventListener("resize", () => {
        return;
      });
    };
  }, []);

  //HANDLE SLIDE CHANGE
  useEffect(() => {
    const slideItems = document.getElementsByClassName("news-item");
    const slideDots = document.getElementsByClassName("news-item__dot");

    let i = 1;
    getIntervalTime.current = setInterval(() => {
      const prev = i - 1 < 0 ? slideItems.length - 1 : i - 1;
      const next = i + 1 >= slideItems.length ? 0 : i + 1;
      const last = prev - 1 < 0 ? slideItems.length - 1 : prev - 1;

      slideItems[last].classList.remove("news-item--previos");
      slideItems[last].classList.remove("news-item--last");
      slideItems[last].classList.add("news-item--last");

      slideItems[prev].classList.remove("news-item--selected");
      slideItems[prev].classList.remove("news-item--previos");
      slideItems[prev].classList.add("news-item--previos");

      slideItems[i].classList.remove("news-item--next");
      slideItems[i].classList.remove("news-item--selected");
      slideItems[i].classList.add("news-item--selected");

      slideItems[next].classList.remove("news-item--last");
      slideItems[next].classList.remove("news-item--next");
      slideItems[next].classList.add("news-item--next");

      slideDots[prev].classList.remove("news-item__dot--selected");
      slideDots[i].classList.add("news-item__dot--selected");

      i++;

      if (i >= slideItems.length) {
        i = 0;
      }
    }, 4000);

    return () => {
      if (getIntervalTime.current) {
        clearInterval(getIntervalTime.current);
      }
    };
  }, []);

  const SlideItem = (props) => {
    const { item, classes } = props;

    return (
      <div
        className={`news-item ${classes}`}
        style={{ width: `${childrenWidth}%` }}
      >
        <img
          className="w-full h-full duration-300 lg:rounded-2xl sm:rounded-xl rounded-none shadow cursor-pointer"
          src={item.banner}
        />
      </div>
    );
  };

  return (
    <div className="w-full oveflow-x-hidden">
      <div className="news-item--wrapped">
        <div className="news-item--sub" style={{ width: `${childrenWidth}%` }}>
          <img
            className="w-full"
            src="https://photo-zmp3.zadn.vn/banner/6/0/a/6/60a6de1c19a7e7c754ea1c7e72af24d1.jpg"
          />
        </div>

        {items.map((item, index) => {
          if (index === 0) {
            return SlideItem({
              item,
              classes: "news-item--selected",
            });
          }

          if (index === 1) {
            return SlideItem({
              item,
              classes: "news-item--next",
            });
          }

          if (index === 2) {
            return SlideItem({
              item,
              classes: "news-item--last",
            });
          }

          if (index === 3) {
            return SlideItem({
              item,
              classes: "news-item--last",
            });
          }

          if (index === 4) {
            return SlideItem({
              item,
              classes: "news-item--previos",
            });
          }
        })}
      </div>

      <div className="flex justify-center">
        <ul className="news-item__dot--wrapped">
          {items.map((item, index) => {
            if (index === 0) {
              return (
                <li className="news-item__dot news-item__dot--selected"></li>
              );
            }

            return <li className="news-item__dot"></li>;
          })}
        </ul>
      </div>
    </div>
  );
};

export default SliderNews;
