import React from "react";
import { BrowserRouter as Link, NavLink, useLocation } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import classNames from "classnames";

import AsideSelected from "../AsideSelected/index";

const NavLinks = (props) => {
  const { name, path, icon, hidden } = props;
  let location = useLocation();

  return (
    <NavLink
      to={path}
      className={classNames(
        "py-3 px-6 relative flex items-center cursor-pointer text-gray-600 hover:text-pink-400 duration-100 font-sm",
        { "text-pink-400": location.pathname === path }
      )}
    >
      {location.pathname === path ? <AsideSelected /> : ""}
      <FontAwesomeIcon icon={icon} className="mr-4 text-xl z-1" />
      <span
        className={classNames("inline whitespace-nowrap z-1", {
          hidden,
        })}
      >
        {name}
      </span>
    </NavLink>
  );
};

export default NavLinks;
