import React, { useState } from "react";
import axios from "axios";
import classNames from "classnames";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faAt, faLock } from "@fortawesome/free-solid-svg-icons";
import avatar from "../../assets/images/default-avatar.jpg";

import { URL } from "../../constants/url.constant";

const SignUp = (props) => {
  const { onSignIn, onUserInfo } = props;
  const [error, setError] = useState({
    type: "",
    msg: "",
  });

  const handleSubmit = async (event) => {
    event.preventDefault();
    const email = document.getElementById("email").value;
    const password = document.getElementById("password").value;

    if (!email) {
      setError({
        type: "warning",
        msg: "Vui lòng nhập email.",
      });
      return;
    }

    if (!password) {
      setError({
        type: "warning",
        msg: "Vui lòng nhập mật khẩu.",
      });
      return;
    }

    const user = await axios.post(URL + "/users", {
      type: "signin",
      userInfo: {
        email,
        password,
      },
    });

    console.log(user.data.isSuccess);

    if (user.data.isSuccess) {
      onUserInfo(user.data.userInfo);
      onSignIn();
      return;
    }

    setError({
      type: "danger",
      msg: user.data.msg,
    });
  };

  return (
    <form onSubmit={handleSubmit} className="px-4 flex-1">
      <div className="flex flex-col items-center">
        <img className="w-24 h-24 flex-none rounded-full" src={avatar} />
      </div>

      {error.msg.length > 0 ? (
        <div
          className={classNames("mt-2 p-2 italic text-sm text-center rounded", {
            "bg-yellow-500": error.type === "warning",
            "bg-red-500": error.type === "danger",
            "text-white": error.type === "danger",
          })}
        >
          {error.msg}
        </div>
      ) : (
        ""
      )}

      <div className="mt-4 mx-2 pt-2 pb-1 flex items-center text-gray-500 border-b border-gray-200">
        <FontAwesomeIcon className="ml-2 mr-4 flex-none" icon={faAt} />
        <input
          id="email"
          type="email"
          className="mr-2 flex-1 outline-none border-0"
          placeholder="Email"
        />
      </div>

      <div className="mt-6 mx-2 pt-2 pb-1 flex items-center text-gray-500 border-b border-gray-200">
        <FontAwesomeIcon className="mx-2 mr-4 flex-none" icon={faLock} />
        <input
          id="password"
          type="password"
          className="mr-2 flex-1 outline-none border-0"
          placeholder="Password"
        />
      </div>

      <div className="mb-4 mt-3 border-b border-gray-200">
        <div className="mb-3 text-blue-400 hover:underline cursor-pointer text-center">
          Quên mật khẩu?
        </div>
      </div>

      <button className="mb-4 py-2 w-full rounded-lg bg-blue-500 hover:opacity-80 text-white border-0 duration-200 outline-none focus:outline-none">
        Đăng Nhập
      </button>
    </form>
  );
};

export default SignUp;
