import React, { useEffect, useState } from "react";
import classNames from "classnames";

import avatar from "../../../assets/images/default-avatar.jpg";

import "./style.scss";

const Comment = (props) => {
  const {
    author: { name, id },
    content,
    userId,
  } = props;

  return (
    <div
      className={classNames("comment--wrapped", {
        "comment--wrapped--right": id === userId,
        "comment--wrapped--left": id !== userId,
      })}
    >
      <img className="comment--wrapped__avatar" src={avatar} />

      <span
        className={classNames("comment--wrapped__info", {
          "comment--wrapped__info--right": id === userId,
          "comment--wrapped__info--left": id !== userId,
        })}
      >
        <span
          className={classNames("comment--wrapped__info__author", {
            "comment--wrapped__info__author--right": id === userId,
            "comment--wrapped__info__author--left": id !== userId,
          })}
        >
          {name}
        </span>
        <div className="comment--wrapped__info__content">{content}</div>
      </span>
    </div>
  );
};

export default Comment;
