import React from "react";
import classNames from "classnames";

import avatar from "../../assets/images/default-avatar.jpg";

import Search from "./Search/index-hook";

export default function Header(props) {
  const { onPlayList, onTop, onUserForm } = props;

  return (
    <header
      className={classNames(
        "w-full h-18 md:pl-64 sm:pl-18 sm:pr-4 px-0 flex items-center fixed duration-300 z-20",
        { "bg-sweet-pink": !onTop, "shadow-lg": !onTop }
      )}
    >
      <Search onTop={onTop} onPlayList={onPlayList} />

      <div
        onClick={onUserForm}
        className="h-10 w-10 ml-auto sm:mr-0 mr-4 rounded-full border flex-none cursor-pointer"
      >
        <img className="w-full h-full rounded-full" src={avatar} />
      </div>
    </header>
  );
}
