import React, { Component } from "react";

export default class NewSongItem extends Component {
  render() {
    const { item, onPlayList } = this.props;

    const handleClick = () => {
      onPlayList(item.encodeId);
    };

    return (
      <li
        onClick={handleClick}
        className="w-full p-3 sm:rounded-xl rounded-none hover:bg-white hover:bg-opacity-5 duration-200 hover:bg-white hover:bg-opacity-40 duration-200 truncate cursor-pointer"
      >
        <div className="flex overflow-hidden">
          <img className="mr-3 w-10 rounded shadow" src={item.thumbnail} />
          <div>
            <p className="text-sm font-bold text-gray-700">{item.title}</p>
            <p className="text-xs text-gray-500">{item.artistsNames}</p>
          </div>
        </div>
      </li>
    );
  }
}
