import React, { useState, useEffect } from "react";
import axios from "axios";

import callHomeAPI from "../../utils/callHomeAPI";

import SliderNews from "../../components/Main/SliderNews/index";
import NewSong from "../../components/Main/NewSong/index";
import Chart from "../../components/Main/Chart/index";
import PlayList from "../../components/Main/PlayList/index";
import HubList from "../../components/Main/HubList/index";

import "./style.scss";
import { URL } from "../../constants/url.constant";

const Home = (props) => {
  const { onPlayList } = props;
  const [slideNews, setSlideNews] = useState([]);
  const [newSongs, setNewSongs] = useState([]);
  const [topSongs, setTopSongs] = useState([]);
  const [charts, setCharts] = useState({});
  const [list_1, setList_1] = useState([]);
  const [list_2, setList_2] = useState([]);
  const [weekChart, setWeekChart] = useState([]);
  const [hub, setHub] = useState(null);

  useEffect(() => {
    const getHomeData_1 = async () => {
      const data = await callHomeAPI(1);
      if (data.status === "success") {
        let playList = [];
        data.doc.items.map((item) => {
          if (item.sectionType === "banner") {
            setSlideNews(item.items);
          }

          if (item.sectionType === "playlist") {
            playList.push(item);
          }
        });

        setList_1(playList);
      }
    };

    const getHomeData_2 = async () => {
      const data = await callHomeAPI(2);
      if (data.status === "success") {
        let playList = [];
        data.doc.items.map((item) => {
          if (item.sectionType === "genre_mood") {
            setHub(item);
          }

          if (item.sectionType === "RTChart") {
            setCharts({
              status: "success",
              chart: item.chart,
            });
          }

          if (item.sectionType === "playlist") {
            playList.push(item);
          }

          if (item.sectionType === "weekChart") {
            setWeekChart(item.items);
          }
        });

        setList_2(playList);
      }
    };

    const getHomeData_3 = async () => {
      const data = await callHomeAPI(3);
      if (data.status === "success") {
        data.doc.items.map((item) => {
          if (item.sectionType === "newReleaseChart") {
            setNewSongs(item.items);
          }
        });
      }
    };

    getHomeData_1();
    getHomeData_2();
    getHomeData_3();
    handleCallTopSongAPI();
  }, []);

  const handleCallTopSongAPI = async () => {
    const data = await axios
      .post(URL + "/topSongs", { count: 3 })
      .then((res) => res.data);

    if (data.status === "success") {
      setTopSongs(data.doc);
    }
  };

  return (
    <div className="w-full mt-20 sm:pb-20 pb-14 sm:px-6 px-0 overflow-x-hidden">
      {slideNews.length > 0 ? <SliderNews items={slideNews} /> : ""}

      {list_1.length > 0
        ? list_1.map((list, index) => (
            <PlayList
              key={list.sectionId}
              index={index}
              list={list}
              onPlayList={onPlayList}
            />
          ))
        : ""}

      {hub ? <HubList list={hub.items} index={0} /> : ""}

      {topSongs.length > 0 && charts.status === "success" ? (
        <Chart
          topSongItems={topSongs}
          chart={charts.chart}
          onPlayList={onPlayList}
        />
      ) : (
        ""
      )}

      <div className="lg:flex block sm:mt-0 mt-4">
        {weekChart.length > 0
          ? weekChart.map((item) => (
              <div className="flex-1 px-2 my-2">
                <div className="cursor-pointer week-chart--wrapped">
                  <img className="w-full rounded-md" src={item.cover} />
                </div>
              </div>
            ))
          : ""}
      </div>

      {list_2.length > 0
        ? list_2.map((list, index) => (
            <PlayList
              key={list.sectionId}
              index={index}
              list={list}
              onPlayList={onPlayList}
            />
          ))
        : ""}

      {newSongs.length > 0 ? (
        <NewSong items={newSongs} onPlayList={onPlayList} />
      ) : (
        ""
      )}
    </div>
  );
};

export default Home;
