import React from 'react'
import banner from '../../assets/images/under-construction.gif'

const UnderConstruction = (props) => {
    return(
        <div className="w-full h-full relative">
            <img src={banner} className="absolute top-1/2 left-1/2" style={{transform: 'translate(-50%, -50%)'}}/>
        </div>
    )
}

export default UnderConstruction