import React, { useEffect, useState } from "react";
import classNames from "classnames";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faYoutube } from "@fortawesome/free-brands-svg-icons";
import {
  faPoll,
  faMusic,
  faChevronRight,
  faChevronLeft,
  faRecordVinyl,
  faIcons,
  faStar,
  faIdCard,
  faListAlt,
} from "@fortawesome/free-solid-svg-icons";

import NavLinks from "./NavLinks/index";

import logoFullA from '../../assets/images/logo-full-1.png'
import logoFullB from '../../assets/images/logo-full-2.png'
import logoShort from '../../assets/images/logo-short.svg'


const Aside = (props) => {
  const [isFullWidth, setIsFullWidth] = useState(
    window.innerWidth >= 768 ? true : false
  );

  const pages1 = [
    {
      name: "Cá Nhân",
      path: "/user",
      icon: faListAlt,
    },

    {
      name: "Khám Phá",
      path: "/",
      icon: faRecordVinyl,
    },

    {
      name: "Top Songs",
      path: "/top-songs",
      icon: faPoll,
    },

    {
      name: "Theo Dõi",
      path: "/artists",
      icon: faIdCard,
    },
  ];

  const pages2 = [
    {
      name: "Nhạc Mới",
      path: "/new-release",
      icon: faMusic,
    },

    {
      name: "Thể Loại",
      path: "/hub",
      icon: faIcons,
    },

    {
      name: "Top 100",
      path: "/Top100",
      icon: faStar,
    },

    {
      name: "MV",
      path: "/mv",
      icon: faYoutube,
    },
  ];

  useEffect(() => {
    window.addEventListener("resize", handleResize);
  }, []);

  function handleResize() {
    const bool = window.innerWidth > 768 ? true : false;
    setIsFullWidth(bool);
  }

  return (
    <div
      className={classNames(
        "w-18 h-screen sm:pb-20 sm:inline hidden fixed bg-egg shadow-xl duration-300 z-30 overflow-hidden",
        { "w-64": isFullWidth }
      )}
    >
      <div className="h-18 md:px-6">
        <div className={classNames("h-full flex items-center", {"pl-4": isFullWidth, 'justify-center': !isFullWidth})}>
          {isFullWidth ? (
            (
              <div className="h-full flex items-center">
                <img className="h-1/2 mr-2" src={logoFullA} />
                <img className="h-1/4 mt-2" src={logoFullB} />
              </div>
            )
          ) : <img className="w-12" src={logoShort} />}
        </div>
      </div>

      <ul className="w-full flex flex-col">
        {pages1
          ? pages1.map((page) => (
              <NavLinks
                name={page.name}
                path={page.path}
                icon={page.icon}
                hidden={!isFullWidth}
              />
            ))
          : ""}

        <div className="mx-4 my-3 h-0.5 bg-black bg-opacity-5"></div>

        {pages2
          ? pages2.map((page) => (
              <NavLinks
                name={page.name}
                path={page.path}
                icon={page.icon}
                hidden={!isFullWidth}
              />
            ))
          : ""}
      </ul>

      <span
        onClick={() => {
          setIsFullWidth(!isFullWidth);
        }}
        className={classNames(
          "w-10 h-10 absolute bottom-24 hover:bg-opacity-70 md:hidden flex justify-center items-center rounded-full duration-200 cursor-pointer",
          { "right-3": isFullWidth, "left-1/2": !isFullWidth }
        )}
        style={
          !isFullWidth
            ? {
                transform: "translateX(-50%)",
                background: "rgba(0, 0, 0, 0.05)",
              }
            : { background: "rgba(0, 0, 0, 0.05)" }
        }
      >
        {!isFullWidth ? (
          <FontAwesomeIcon icon={faChevronRight} className="text-gray-500" />
        ) : (
          <FontAwesomeIcon icon={faChevronLeft} className="text-gray-500" />
        )}
      </span>
    </div>
  );
};

export default Aside;
