import React from "react";
import { BrowserRouter as Route, Switch, Redirect } from "react-router-dom";

import Home from "../../pages/Home/index";
import TopSongs from "../../pages/TopSongs/index";
import NewRelease from "../../pages/NewRelease/index";
import Top100 from "../../pages/Top100/index";
import Hub from "../../pages/Hub/index";
import User from "../../pages/User/index";
import UnderConstruction from '../../pages/UnderConstruction/index'

const Main = React.memo((props) => {
  const {
    onPlayList,
    userInfo,
    onUserForm__visible,
    onUserPlaylist__play,
  } = props;

  return (
    <main className="w-full mx-auto mb-20">
      <Switch>
        <Redirect exact from="/home" to="/" />

        <Redirect exact from="/artists" to="/under-construction" />

        <Redirect exact from="/mv" to="/under-construction" />

        <Route path="/under-construction">
          <UnderConstruction />
        </Route>

        <Route path="/user">
          <User
            onPlayList={onPlayList}
            userInfo={userInfo}
            onUserForm__visible={onUserForm__visible}
            onUserPlaylist__play={onUserPlaylist__play}
          />
        </Route>

        <Route path="/hub">
          <Hub onPlayList={onPlayList} />
        </Route>

        <Route path="/new-release">
          <NewRelease onPlayList={onPlayList} />
        </Route>

        <Route path="/top-songs">
          <TopSongs onPlayList={onPlayList} />
        </Route>

        <Route path="/top100">
          <Top100 onPlayList={onPlayList} />
        </Route>

        <Route path="/">
          <Home onPlayList={onPlayList} />
        </Route>
      </Switch>
    </main>
  );
});

export default Main;
