import React, { useState, useEffect } from "react";
import classNames from "classnames";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faChevronLeft,
  faChevronRight,
} from "@fortawesome/free-solid-svg-icons";

import Item from "./item/index";

const HubList = (props) => {
  const { list, index } = props;
  const [childrenWidth, setChildrenWidth] = useState(0);
  const [margin, setMargin] = useState(0);
  const [isShowChevron, setIsShowChevron] = useState(true);
  const [isShowChevronRight, setIsShowChevronRight] = useState(isShowChevron);

  //HANDLE CHILDREN WIDTH
  useEffect(() => {
    const listElement = document.getElementsByClassName("hub-list")[0];

    const handleResize = () => {
      setMargin(0);
      const parentWidth = listElement.clientWidth;
      let itemWidth = 0;

      if (parentWidth >= 1024) {
        itemWidth = parentWidth / 4;
        setChildrenWidth(itemWidth);
      } else if (parentWidth >= 798) {
        itemWidth = listElement.clientWidth / 3;
        setChildrenWidth(itemWidth);
      } else {
        itemWidth = parentWidth / 2;
        setChildrenWidth(itemWidth);
      }

      if (parentWidth < itemWidth * (list.length / 2)) {
        setIsShowChevron(true);
        return;
      }
      setIsShowChevron(false);
    };

    handleResize();
    new ResizeObserver(handleResize).observe(listElement);
  }, []);

  const handleChevronLeftClick = () => {
    setIsShowChevronRight(true);
    const parentWidth = document.getElementsByClassName("hub-list")[0]
      .clientWidth;

    if (-margin >= parentWidth) {
      return setMargin(margin + parentWidth);
    }
    setMargin(0);
  };

  const handleChevronRightClick = () => {
    const parentWidth = document.getElementsByClassName("hub-list")[0]
      .clientWidth;
    const className = "hub-list__items--wrapped";
    const currentWidth = document.getElementsByClassName(className)[index]
      .clientWidth;

    if (currentWidth + margin > parentWidth * 2) {
      return setMargin(margin - parentWidth);
    }

    setIsShowChevronRight(false);
    setMargin(margin - (currentWidth + margin - parentWidth));
  };

  return (
    <div className="sm:my-4 mt-4">
      <div className="mb-1 sm:p-4 sm:pb-0 px-2 flex justify-between">
        <h1 className="text-lg text-gray-700 font-bold capitalize">
          {/* {list.title ? list.title : list.genre.name} */}
          Tâm Trạng Và Hoạt Động
        </h1>
        <div className={classNames({ hidden: !isShowChevron })}>
          <FontAwesomeIcon
            onClick={handleChevronLeftClick}
            className={classNames(
              "mr-4 hover:text-gray-900 text-lg font-bold cursor-pointer",
              {
                "pointer-events-none text-gray-400": margin === 0,
                "text-gray-700": !(margin === 0),
              }
            )}
            icon={faChevronLeft}
          />
          <FontAwesomeIcon
            onClick={handleChevronRightClick}
            className={classNames(
              "hover:text-gray-900 text-lg font-bold cursor-pointer",
              {
                "pointer-events-none text-gray-400": !isShowChevronRight,
                "text-gray-700": isShowChevronRight,
              }
            )}
            icon={faChevronRight}
          />
        </div>
      </div>

      <div className="w-full overflow-hidden hub-list">
        <div
          className="flex duration-300 hub-list__items--wrapped"
          style={{
            width: childrenWidth * (list.length / 2),
            marginLeft: margin,
          }}
        >
          {list.map((item, index) => {
            if (index === 0 || index % 2 === 0) {
              if (list[index + 1]) {
                return (
                  <div>
                    <Item
                      key={list[index].encodeId}
                      data={list[index]}
                      width={childrenWidth}
                    />
                    <Item
                      key={list[index + 1].encodeId}
                      data={list[index + 1]}
                      width={childrenWidth}
                    />
                  </div>
                );
              }
            }
          })}
        </div>
      </div>
    </div>
  );
};

export default HubList;
