import React, { useState, useEffect } from "react";
import axios from "axios";

import HubList from "../../components/Main/HubList/index";
import PlayList from "../../components/Main/PlayList/index";
import "./style.scss";
import { URL } from "../../constants/url.constant";

const Hub = (props) => {
  const { onPlayList } = props;
  const [hubList, setHubList] = useState([]);
  const [genre, setGenre] = useState([]);

  useEffect(() => {
    const getHub = async () => {
      const data = await axios.get(URL + "/hub").then((res) => res.data);

      if (data.status === "success") {
        setHubList(data.doc.topic);
        setGenre(data.doc.genre);
        return;
      }

      console.log("error");
    };

    getHub();
  }, []);

  return (
    <div className="hub--wrapped">
      <div className="hub__img--wrapped">
        <img
          className="hub__img"
          src="https://photo-zmp3.zadn.vn/cover/e/9/0/a/e90a10adebb962d8f9f38c5aa4fe83dc.jpg"
        />
      </div>

      {hubList.length > 0 ? (
        <HubList list={hubList} index={0} onPlayList={onPlayList} />
      ) : (
        ""
      )}

      {genre.length > 0
        ? genre.map((list, index) => (
            <PlayList
              key={list.encodeId}
              index={index}
              list={list}
              onPlayList={onPlayList}
            />
          ))
        : ""}
    </div>
  );
};

export default Hub;
