import React, { Component } from "react";
import classNames from "classnames";

export default class TopSong extends Component {
  handleRankSelected__onHover = (event) => {
    const { onCount, item } = this.props;
    const element = event.target;

    const selected = document.getElementsByClassName("rank__selected")[0];
    if (selected) {
      selected.classList.remove("rank__selected");
    }

    element.classList.add("rank__selected");
    onCount(item.position - 1);
  };

  render() {
    const { item, totalScore, onPlayList } = this.props;

    return (
      <div
        onClick={() => {
          onPlayList([item.playlist_id]);
        }}
        onMouseEnter={this.handleRankSelected__onHover}
        className={classNames(
          "sm:mb-2 mb-1 sm:p-4 p-2 flex bg-white bg-opacity-30 hover:bg-opacity-50 sm:rounded-xl cursor-pointer duration-200 rank",
          { rank__selected: item.position === 1 }
        )}
      >
        <span
          className={classNames("sm:mr-4 mr-2 flex items-center top-rank", {
            "top-1": item.position === 1,
            "top-2": item.position === 2,
            "top-3": item.position === 3,
          })}
        >
          {item.position}
        </span>
        <span className="mr-4 flex rounded flex-grow truncate">
          <img className="w-14 mr-2 rounded" src={item.thumbnail} />
          <span className="py-2">
            <div className="text-sm text-white font-medium">{item.title}</div>
            <div className="text-xs text-gray-100">{item.artists_names}</div>
          </span>
        </span>
        <span className="flex items-center text-white font-bold">
          {Math.round((item.total / totalScore) * 100) + "%"}
        </span>
      </div>
    );
  }
}
